/*
*个人中心组建
*/
<template xmlns:v-bind="http://www.w3.org/1999/xhtml">

<div>
  <div class="loading">
    <span></span>
    <span></span>
    <span></span>
    <span></span>
    <span></span>
  </div>

  <div style="margin-top: 20px;">转换中……</div>

</div>
</template>


<script>
  export default{
    name: 'loading',
    data() {
      return {


      }
    },
    components: {},
    computed: {},
    props: {

    },
    created(){
    },
    mounted(){

    },
    methods: {

    },
    beforedestroy(){

    },
    watch: {

    }
  }
</script>

<style scoped>
  .loading{
    width: 80px;
    height: 40px;
    margin: 0 auto;
    margin-top:100px;
    transform: scaleX(1.5);
  }
  .loading span{
    display: inline-block;
    width: 8px;
    height: 100%;
    border-radius: 4px;
    background: #3385ff;
    -webkit-animation: load 1s ease infinite;
  }
  @-webkit-keyframes load{
    0%,100%{
      height: 40px;
      background: #3385ff;
    }
    50%{
      height: 70px;
      margin: -15px 0;
      background: lightblue;
    }
  }
  .loading span:nth-child(2){
    -webkit-animation-delay:0.2s;
  }
  .loading span:nth-child(3){
    -webkit-animation-delay:0.4s;
  }
  .loading span:nth-child(4){
    -webkit-animation-delay:0.6s;
  }
  .loading span:nth-child(5){
    -webkit-animation-delay:0.8s;
  }
</style>



